  &__carousel {
    overflow: visible;
    .swiper-wrapper {
      flex-direction: row;
      row-gap: 140px;
    }
    .swiper-slide {
      height: calc((100% - 30px) / 2) !important;
      margin-top: 0 !important;
      max-width: 375px;
      @media(max-width: $xl){
      
      }
      @media(max-width: $lg){
      
      }
      @media(max-width: $md){
        max-width: 290px;
      }
      @media(max-width: $sm){
      
      }
      @media(max-width: $xs){
      
      }
      
      &::after {
        right: -80px;
        top: 0;
        bottom: 0;
        position: absolute;
        content: '';
        width: 1px;
        background: $text;
        @media(max-width: $xl){
          right: -40px;
        
        }
        @media(max-width: $lg){
        }
        @media(max-width: $md){
          right: -20px;
        }
        @media(max-width: $sm){
        
        }
        @media(max-width: $xs){
        
        }
        
      }
      &:nth-of-type(3n) {
        
        &::after {
          @media(min-width: $md){
            display: none;
            
          }
        }
      }
      &:nth-of-type(4) {
        
        &::after {
          @media(min-width: $md){
          transform: translateY(-160px);
          }
        }
      }
      &:nth-of-type(2) {
        
        &::after {
          @media(min-width: $md){
          transform: translateY(160px);
          }
        }
      }
    }
  }

